import PropTypes from 'prop-types';

import { useTheme } from '@material-ui/core/styles';
import { Grid, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from '@material-ui/core';

import Scrollbar from '../Scrollbar';

const TABLE_HEAD = [
  { id: 'name', label: 'Name', alignRight: false },
  { id: 'type', label: 'Type', alignRight: false },
  { id: 'weeklyDispensed', label: 'Weekly Dispensed', alignRight: false },
  { id: 'dispensedWeeklyTokenValue', label: 'Dispensed Weekly (TokenValue)', alignRight: false },
  { id: 'averageTokenValue', label: 'Average Token Value', alignRight: false },
  { id: 'weeklyEarners', label: 'Weekly Earners', alignRight: false },
  { id: 'active', label: 'Active', alignRight: false },
  { id: 'location', label: 'Location In App', alignRight: false },
];

RewardListTable.propTypes = {
  rewardList: PropTypes.array,
};

function RewardListTable({ rewardList = [] }) {
  const theme = useTheme();
  return (
    <Grid container spacing={2} sx={{ mt: theme.spacing(8) }}>
      <Grid item xs={12}>
        <Typography variant="h5">Reward List</Typography>
      </Grid>
      <Grid item xs={12}>
        <Scrollbar>
          <TableContainer sx={{ minWidth: 800 }}>
            <Table aria-label="reward table">
              <TableHead>
                <TableRow>
                  {TABLE_HEAD.map((headCell) => (
                    <TableCell key={headCell.id} align={headCell.alignRight ? 'right' : 'left'}>
                      {headCell.label}
                    </TableCell>
                  ))}
                </TableRow>
              </TableHead>
              <TableBody>
                {rewardList.map((row) => (
                  <TableRow key={row.name}>
                    <TableCell>{row.name}</TableCell>
                    <TableCell>{row.type}</TableCell>
                    <TableCell>{row.weeklyDispensed}</TableCell>
                    <TableCell>{row.dispensedWeeklyTokenValue}</TableCell>
                    <TableCell>{row.averageTokenValue}</TableCell>
                    <TableCell>{row.weeklyEarners}</TableCell>
                    <TableCell>{row.active ? 'Yes' : 'No'}</TableCell>
                    <TableCell>{row.location}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Scrollbar>
      </Grid>
    </Grid>
  );
}

export default RewardListTable;
