import React from 'react';
import PropTypes from 'prop-types';
import { useSnackbar } from 'notistack5';
import * as Yup from 'yup';
import { Form, FormikProvider, useFormik } from 'formik';
import { Box, Card, CardContent, CardHeader, Grid, InputAdornment, Stack, TextField } from '@material-ui/core';
import { LoadingButton } from '@material-ui/lab';
import { Save as SaveIcon } from '@material-ui/icons';

const SettingsSchema = Yup.object().shape({
  tokenValue: Yup.number().required('This field is required.'),
  starValueInTokens: Yup.number().required('This field is required.'),
  keyValueInTokens: Yup.number().required('This field is required.'),
  boosterValueInTokens: Yup.number().required('This field is required.'),
});

const RewardSettings = ({ currentSettings }) => {
  const { enqueueSnackbar } = useSnackbar();

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      tokenValue: currentSettings?.tokenValue || 5,
      starValueInTokens: currentSettings?.starValueInTokens || 5,
      keyValueInTokens: currentSettings?.keyValueInTokens || 5,
      boosterValueInTokens: currentSettings?.boosterValueInTokens || 5,
    },
    validationSchema: SettingsSchema,
    onSubmit: async (values, { setSubmitting, resetForm, setErrors }) => {
      alert(JSON.stringify(values));
      try {
        resetForm();
        setSubmitting(false);
        enqueueSnackbar('Update success', { variant: 'success' });
      } catch (error) {
        console.error(error);
        setSubmitting(false);
        setErrors(error);
      }
    },
  });

  const { errors, touched, handleSubmit, getFieldProps, isSubmitting } = formik;

  return (
    <FormikProvider value={formik}>
      <Form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          <Grid item md={12}>
            <Card>
              <CardHeader title="Reward Settings" />
              <CardContent>
                <Stack spacing={2}>
                  <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                    <TextField
                      fullWidth
                      size="small"
                      label="Token Value in USD"
                      InputProps={{
                        endAdornment: <InputAdornment position="end">USD</InputAdornment>,
                      }}
                      {...getFieldProps('tokenValue')}
                      error={Boolean(touched.tokenValue && errors.tokenValue)}
                      helperText={touched.tokenValue && errors.tokenValue}
                    />
                    <TextField
                      fullWidth
                      size="small"
                      label="Star Value in Tokens"
                      InputProps={{
                        endAdornment: <InputAdornment position="end">tokens</InputAdornment>,
                      }}
                      {...getFieldProps('starValueInTokens')}
                      error={Boolean(touched.starValueInTokens && errors.starValueInTokens)}
                      helperText={touched.starValueInTokens && errors.starValueInTokens}
                    />
                  </Stack>
                  <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                    <TextField
                      fullWidth
                      size="small"
                      label="Key Value in Tokens"
                      InputProps={{
                        endAdornment: <InputAdornment position="end">tokens</InputAdornment>,
                      }}
                      {...getFieldProps('keyValueInTokens')}
                      error={Boolean(touched.keyValueInTokens && errors.keyValueInTokens)}
                      helperText={touched.keyValueInTokens && errors.keyValueInTokens}
                    />
                    <TextField
                      fullWidth
                      size="small"
                      label="Booster Value in Tokens"
                      InputProps={{
                        endAdornment: <InputAdornment position="end">tokens</InputAdornment>,
                      }}
                      {...getFieldProps('boosterValueInTokens')}
                      error={Boolean(touched.boosterValueInTokens && errors.boosterValueInTokens)}
                      helperText={touched.boosterValueInTokens && errors.boosterValueInTokens}
                    />
                  </Stack>
                  <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
                    <LoadingButton type="submit" variant="contained" loading={isSubmitting} endIcon={<SaveIcon />}>
                      Save
                    </LoadingButton>
                  </Box>
                </Stack>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Form>
    </FormikProvider>
  );
};

RewardSettings.propTypes = {
  currentSettings: PropTypes.object,
};

export default RewardSettings;
